const zeros = require('zeros')

const dcmbufferToPng = (metadata, invertRows=false, useWindowing=true) => {
    const {
        BitsAllocated,
        BitsStored,
        Rows: rows,
        Columns: columns,
        WindowCenter: windowCenter,
        WindowWidth: windowWidth,
        PixelData: pixelData
    } = metadata;

    // Create the appropriate pixel array based on BitsAllocated
    const pixelArray = BitsAllocated === 16 ? new Int16Array(pixelData) : new Int8Array(pixelData);

    // Initialize the result array
    const res = zeros([rows, columns]);

    // Precompute windowing values if they exist
    const hasWindowing = useWindowing && windowCenter && windowWidth;
    const windowMin = windowCenter - 0.5 - (windowWidth - 1) / 2;
    const windowMax = windowCenter - 0.5 + (windowWidth - 1) / 2;

    const transformPixelValue = (val) => {
        let transformedValue = val;

        if (hasWindowing) {
            if (val <= windowMin) {
                transformedValue = 0;
            } else if (val > windowMax) {
                transformedValue = 255;
            } else {
                transformedValue = ~~(((val - (windowCenter - 0.5)) / (windowWidth - 1) + 0.5) * 255);
            }
        } else if (BitsAllocated === 16) {
            transformedValue = ((val + 1000) * 2 ** 8) / 2 ** BitsStored;
        }

        return transformedValue;
    };

    // Loop through pixels and populate `res`
    for (let i = 0; i < rows; i++) {
        const rowIndex = invertRows ? rows - i - 1 : i;
        for (let j = 0; j < columns; j++) {
            const val = transformPixelValue(pixelArray[i * columns + j]);
            res.set(rowIndex, j, val);
        }
    }

    return {
        buffer: res,
        height: rows,
        width: columns,
        ext: 'png',
    };
};

const pngToCanvas = (pngBuf) => {
    const coloredBuffer = new Uint8ClampedArray(pngBuf.width * pngBuf.height * 4);
    const buffer = pngBuf.buffer.data;
    buffer.forEach((p, i) => {
        coloredBuffer[4 * i] = p;
        coloredBuffer[4 * i + 1] = p;
        coloredBuffer[4 * i + 2] = p;
        coloredBuffer[4 * i + 3] = 255;
    });

    const canvas = document.createElement('canvas');
    canvas.width = pngBuf.width;
    canvas.height = pngBuf.height;
    const imgData = new ImageData(new Uint8ClampedArray(coloredBuffer), pngBuf.width, pngBuf.height);
    canvas.getContext('2d').putImageData(imgData, 0, 0);

    return canvas;
}

export { dcmbufferToPng, pngToCanvas };
