const DEFAULT_OPTIONS = { order: 2, period: null };

/**
 * Determine the coefficient of determination (r^2) of a fit from the observations
 * and predictions.
 *
 * @param {Array<Array<number>>} data - Pairs of observed x-y values
 * @param {Array<Array<number>>} results - Pairs of observed predicted x-y values
 *
 * @return {number} - The r^2 value, or NaN if one cannot be calculated.
 */
function determinationCoefficient(data, results) {
    const predictions = [];
    const observations = [];

    data.forEach((d, i) => {
        if (d[1] !== null) {
            observations.push(d);
            predictions.push(results[i]);
        }
    });

    const sum = observations.reduce((a, observation) => a + observation[1], 0);
    const mean = sum / observations.length;

    const ssyy = observations.reduce((a, observation) => {
        const difference = observation[1] - mean;
        return a + (difference * difference);
    }, 0);

    const sse = observations.reduce((accum, observation, index) => {
        const prediction = predictions[index];
        const residual = observation[1] - prediction[1];
        return accum + (residual * residual);
    }, 0);

    return 1 - (sse / ssyy);
}

/**
 * Determine the solution of a system of linear equations A * x = b using
 * Gaussian elimination.
 *
 * @param {Array<Array<number>>} matrix - A 2-d matrix of data in row-major form [ A | b ]
 * @param {number} order - How many degrees to solve for
 *
 * @return {Array<number>} - Vector of normalized solution coefficients matrix (x)
 */
function gaussianElimination(matrix, order) {
    const n = matrix.length - 1;
    const coefficients = Array(order);

    for (let i = 0; i < n; i++) {
        let maxRow = i;
        // Find the pivot row
        for (let j = i + 1; j < n; j++) {
            if (Math.abs(matrix[i][j]) > Math.abs(matrix[i][maxRow])) {
                maxRow = j;
            }
        }

        // Swap rows
        for (let k = i; k <= n; k++) {
            [matrix[k][i], matrix[k][maxRow]] = [matrix[k][maxRow], matrix[k][i]];
        }

        // Make upper triangular
        for (let j = i + 1; j < n; j++) {
            for (let k = n; k >= i; k--) {
                // TODO divide by zero
                matrix[k][j] -= (matrix[k][i] * matrix[i][j]) / matrix[i][i];
            }
        }
    }

    // Backward substitution
    for (let j = n - 1; j >= 0; j--) {
        let total = 0;
        for (let k = j + 1; k < n; k++) {
            total += matrix[k][j] * coefficients[k];
        }

        coefficients[j] = (matrix[n][j] - total) / matrix[j][j];
    }

    return coefficients;
}

/**
 * Round a number to a precision, specificed in number of decimal places
 *
 * @param {number} number - The number to round
 * @param {number} precision - The number of decimal places to round to:
 *                             > 0 means decimals, < 0 means powers of 10
 *
 *
 * @return {number} - The number, rounded
 */
function round(number, precision) {
    const factor = 10 ** precision;
    return Math.round(number * factor) / factor;
}

/**
 * The set of all fitting methods
 *
 * @namespace
 */
export const polynomialLSF = (data, order) => {
    const k = order + 1;

    // Initialize matrices for lhs and rhs
    const lhs = Array(k).fill(0);
    const rhs = Array.from({ length: k }, () => Array(k).fill(0));

    // Build the lhs and rhs matrices
    for (let i = 0; i < k; i++) {
        for (let j = 0; j < k; j++) {
            rhs[i][j] = data.reduce((sum, point) => point.y !== null ? sum + point.x ** (i + j) : sum, 0);
        }
        lhs[i] = data.reduce((sum, point) => point.y !== null ? sum + (point.x ** i) * point.y : sum, 0);
    }

    rhs.push(lhs);

    // Calculate coefficients using Gaussian elimination
    const coefficients = gaussianElimination(rhs, k).map(v => v);

    // Define prediction function
    const predict = (x) => coefficients.reduce((sum, coeff, power) => sum + (coeff * x ** power), 0);

    return {
        predict,
        equation: [...coefficients].reverse(),
    };
};
