import { Dentascan } from 'extensions/cornerstone/src/types/Panoramic';

const derivative = (f, x, dx = 1e-5) => {
    return (f(x + dx) - f(x)) / dx;
}

const getSlopeVector = (slope) => {
    const [vx, vy] = [1, slope];
    const vLength = Math.sqrt(1 + slope * slope);
    return [vx / vLength, vy / vLength];
}

export const getTangentVector = (f, x) => {
    const tangentSlope = derivative(f, x);
    return getSlopeVector(tangentSlope);
}

export const getNormalVector = (f, x) => {
    const tangentSlope = derivative(f, x);
    const normalSlope = -1 / tangentSlope;
    return getSlopeVector(normalSlope);
}

const interpolatePoints = (v1, v2, t) => {
    return Math.round(t * v1 + (1 - t) * v2);
}

const interpolateThreePoints = (v1, v2, vMid, tMin, tMax) => {
    return Math.round(tMin * v1 + (tMax - tMin) * vMid + (1 - tMax) * v2);
}

// Get dentascan section at abscissa i for a polynomial fitting curve lsf.
// Length of normal is dentascanThickness
// Dicom of the dentascan desired is defined by its dicomDimensions and its scalarData
export const getDentascanSection = (x, lsfDentalCurve, dicomData, p, dentascanThickness) => {
    // Get normalized vector of normal at x
    let [vxNormal, vyNormal] = getNormalVector(lsfDentalCurve, x);

    // In order to prevent having a flipped normal vector,
    // we need to determine if minPoint lies inside or outside the curve
    // We want minPoint to be the outer point and maxPoint to be the inner point
    const y = lsfDentalCurve(x);
    let minPoint: [number, number] = [x - dentascanThickness / 2 * vxNormal,
                                      y - dentascanThickness / 2 * vyNormal];
    let maxPoint: [number, number] = [x + dentascanThickness / 2 * vxNormal,
                                      y + dentascanThickness / 2 * vyNormal];
    // Swap minPoint and maxPoint and invert normal vectors if minPoint is inner
    if (vyNormal < 0) {
        [minPoint, maxPoint] = [maxPoint, minPoint];
        [vxNormal, vyNormal] = [-vxNormal, -vyNormal];
    }

    // Get values of point along normal between min and max
    const dentascanWidth = 2 * dentascanThickness; // Anti-Aliasing
    const dentascan = new Float32Array(dentascanWidth * p.nbLayers).fill(-1000);
    for (let layer = 0; layer < p.nbLayers; layer++) {
        for (let i = 0; i < dentascanWidth; i += 1) {
            const xi = minPoint[0] + (i / 2) * vxNormal;
            const yi = minPoint[1] + (i / 2) * vyNormal;
            const xj = minPoint[0] + ((i + 1) / 2) * vxNormal;
            const yj = minPoint[1] + ((i + 1) / 2) * vyNormal;
            const [pxi, pxj, pyi, pyj] = [Math.floor(xi), Math.floor(xj), Math.floor(yi), Math.floor(yj)]
            if (xi >= 0 && yi >= 0 && xj < p.layerWidth && yj < p.layerHeight) {
                const dicomIdxI = (p.minLayer + layer) * p.layerSize + pyi * p.layerWidth + pxi;
                if (pxi === pxj && pyi === pyj) {
                    // Same pixel
                    dentascan[layer * dentascanWidth + i] = dicomData[dicomIdxI];
                } else if (pyi === pyj) {
                    // Horizontal interpolation
                    const tx = (pxi < pxj) ? (1 - xi % 1) / vxNormal : -(xi % 1) / vxNormal; // Ratio of segment within pxi
                    const dicomIdxJ = (p.minLayer + layer) * p.layerSize + pyi * p.layerWidth + pxj;
                    dentascan[layer * dentascanWidth + i] = interpolatePoints(dicomData[dicomIdxI], dicomData[dicomIdxJ], tx);
                } else if (pxi === pxj) {
                    // Vertical interpolation
                    const ty = (pyi < pyj) ? (1 - yi % 1) / vyNormal : -(yi % 1) / vyNormal; // Ratio of segment within pyi
                    const dicomIdxJ = (p.minLayer + layer) * p.layerSize + pyj * p.layerWidth + pxi;
                    dentascan[layer * dentascanWidth + i] = interpolatePoints(dicomData[dicomIdxI], dicomData[dicomIdxJ], ty);
                } else {
                    // Diagonal interpolation
                    const tx = (pxi < pxj) ? (1 - xi % 1) / vxNormal : -(xi % 1) / vxNormal;
                    const ty = (pyi < pyj) ? (1 - yi % 1) / vyNormal : -(yi % 1) / vyNormal;
                    const dicomIdxJ = (p.minLayer + layer) * p.layerSize + pyj * p.layerWidth + pxj;
                    const dicomIdxK = tx < ty
                        ? (p.minLayer + layer) * p.layerSize + pyi * p.layerWidth + pxj
                        : (p.minLayer + layer) * p.layerSize + pyj * p.layerWidth + pxi;
                    const tMin = Math.min(tx, ty);
                    const tMax = Math.max(tx, ty);
                    dentascan[layer * dentascanWidth + i] = interpolateThreePoints(dicomData[dicomIdxI], dicomData[dicomIdxJ],
                                                                                   dicomData[dicomIdxK], tMin, tMax);
                }
            }
        }
    }

    const dentascanObject: Dentascan = {
        dentascan: dentascan,
        width: dentascanWidth,
        height: p.nbLayers,
        interval: [minPoint, maxPoint],
        direction: [vxNormal, vyNormal],
        middlePoint: [x, y],
        minLayer: p.minLayer,
    };

    return dentascanObject;
}