import { DicomMetadataStore, PubSubService, Types as OhifTypes, ExtensionManager } from '@ohif/core';
import { CommandsManager } from '../../../../../platform/core/src/classes';
import { Dentascan, PanoramicState } from '../../types/Panoramic';
import { createControlPoints, createPanoramic } from './../../../../default/src/Panels/PanoramicUtils/generatePanoramic'
import { getEnabledElement } from '@cornerstonejs/core';
import {
  annotation as cstAnnotation,
  utilities as cstUtilities,
  Enums as cstEnums,
  segmentation as cstSegmentation,
  BrushTool,
  Types as cstTypes,
} from '@cornerstonejs/tools';
import { Point3 } from '@cornerstonejs/core/src/types';
import { EventTypes } from '@cornerstonejs/tools/src/types';
import { LabelmapSegmentationDataVolume } from '@cornerstonejs/tools/src/types/LabelmapTypes';
import { VolumeData } from '../../types/CornerstoneCacheService';
import vtkImageData from '@kitware/vtk.js/Common/DataModel/ImageData';

function waitCondition<T>(condition: () => T) {
  return new Promise<T>((resolve, reject) => {
      const wait = () => {
          const result = condition();
          if (result) {
              return resolve(result);
          } else {
              setTimeout(() => wait(), 50);
          }
      }
      wait();
  });
}

export default class PanoramicService extends PubSubService {
  private enabled: boolean = false;
  private displayed: boolean = false;
  private editing: boolean = false;
  private processing: boolean = false;
  private subscriptions: any[] = [];
  private panoramicStates: { [displaySetUID: string]: PanoramicState } = {};
  private dentascanIndex: number = 0;

  private readonly commandsManager: CommandsManager;
  private readonly servicesManager: AppTypes.ServicesManager;
  private readonly extensionManager: ExtensionManager;

  public readonly EVENTS = {
    STATUS_DISPLAYED_CHANGED: 'event::PanoramicService:statusDisplayedChanged',
    STATUS_EDITING_CHANGED: 'event::PanoramicService:statusEditingChanged',
    PANORAMIC_STATE_UPDATED: 'event::PanoramicService:panoramicStateUpdated',
    PANORAMIC_CREATED: 'event::PanoramicService:panoramicCreated',
    PANORAMIC_PROCESSING: 'event::PanoramicService:panoramicProcessing',
    DENTASCAN_INDEX_UPDATED: 'event::PanoramicService:dentascanIndexUpdated'
  };

  public static REGISTRATION = {
    name: 'panoramicService',
    create: ({ commandsManager, servicesManager, extensionManager }: OhifTypes.Extensions.ExtensionParams): PanoramicService => {
      return new PanoramicService(commandsManager, servicesManager, extensionManager);
    },
  };

  constructor(commandsManager: CommandsManager, servicesManager: AppTypes.ServicesManager, extensionManager: ExtensionManager) {
    super(PanoramicService.EVENTS);
    this.commandsManager = commandsManager;
    this.servicesManager = servicesManager;
    this.extensionManager = extensionManager;
  }

  public activatePanoramic = () => {
    const { panelService, hangingProtocolService } = this.servicesManager.services;

    // Enable panoramic service and panel
    this.enabled = true;
    const panoramicPanel = '@ohif/extension-default.panelModule.panoramicGenerationPanel';
    panelService.addPanel('right', panoramicPanel, { activate: true });
    panelService.activatePanel(panoramicPanel);

    const event = hangingProtocolService.EVENTS.PROTOCOL_CHANGED;
    hangingProtocolService.subscribe(event, ({ protocol, viewportMatchDetails }) => {
      if (protocol.id === 'panoramicViewport') {
        if (!this.isDisplayed()) {
          const { displaySetInstanceUID } = viewportMatchDetails.get('default')?.displaySetsInfo[0];
          this.handleProtocolEnter(displaySetInstanceUID);
        }
      } else {
        if (this.isDisplayed()) {
          this.handleProtocolExit();
        }
      }
    });

    const eventPanoramic = this.EVENTS.PANORAMIC_CREATED;
    this.subscribe(eventPanoramic, async ({ displaySetUID }) => {
      this.handlePanoramicCreation(displaySetUID);
    });
  }

  public isEnabled = () => {
    return this.enabled;
  }

  public isDisplayed = () => {
    return this.displayed;
  }

  public setDisplayed = (displayed) => {
    this.displayed = displayed;
    this._broadcastEvent(this.EVENTS.STATUS_DISPLAYED_CHANGED, { displayed: displayed });
  }

  public isEditing = () => {
    return this.editing;
  }

  public setEditing = (editing: boolean) => {
    this.editing = editing;
    this._broadcastEvent(this.EVENTS.STATUS_EDITING_CHANGED, { editing: editing });

  }

  public isProcessing = () => {
    return this.processing;
  }

  public getPanoramicState = (displaySetUID: string) => {
    return this.panoramicStates[displaySetUID];
  }

  public addDisplaySetUID = (displaySetUID: string) => {
    if (!this.getPanoramicState(displaySetUID)) {
      this.panoramicStates[displaySetUID] = undefined;
    }
  }

  public setPanoramicState = (displaySetUID: string, panoramicState: PanoramicState) => {
    this.panoramicStates[displaySetUID] = panoramicState;
    this._broadcastEvent(this.EVENTS.PANORAMIC_STATE_UPDATED, { displaySetUID: displaySetUID });
  }

  public setAnnotationVisibility = (displaySetUID: string, annotationUIDs: string[], visible: boolean) => {
    this.panoramicStates[displaySetUID].annotationVisibility = visible;
    for (const annotationUID of annotationUIDs) {
      cstAnnotation.visibility.setAnnotationVisibility(annotationUID, visible);
    }
    this._broadcastEvent(this.EVENTS.PANORAMIC_STATE_UPDATED, { displaySetUID: displaySetUID });
  }

  public setDentascanIndex = (displaySetUID: string, dentascanIndex: number) => {
    this.dentascanIndex = dentascanIndex;
    this._broadcastEvent(this.EVENTS.DENTASCAN_INDEX_UPDATED, { displaySetUID: displaySetUID });
  }

  // -------------------- PANORAMIC CALCULATION -------------------- //

  public _getImageId = (displaySetUID: string) => {
    const { displaySetService } = this.servicesManager.services;

    const displaySet = displaySetService.getDisplaySetByUID(displaySetUID);

    // Get volume from where we want to compute panoramic on
    const baseVolumeSeries = DicomMetadataStore.getSeries(displaySet.StudyInstanceUID, displaySet.SeriesInstanceUID);
    const frameNumber = baseVolumeSeries.instances[0].frameNumber;
    const baseId = baseVolumeSeries.instances[0].imageId + (frameNumber ? '&frame=' + frameNumber : '');

    return baseId;
  }

  public _getControlPoints = (displaySetUID: string, imageData: vtkImageData, dimensions: number[]) => {
    const { measurementService } = this.servicesManager.services;

    const controlPointsInViewport = measurementService.getMeasurements().filter(
      m => m.toolName === 'ArrowAnnotate' && m.metadata.handmade && m.displaySetInstanceUID === displaySetUID
    );
    if (!controlPointsInViewport.length) {
      return [];
    }

    // Regenerate points from annoted points in viewport
    let controlPointsInVolume = controlPointsInViewport[0].points.map(point => {
      let [x, y] = imageData.worldToIndex(point).map(Math.round) as Point3;
      x = Math.min(Math.max(x, 0), dimensions[0]);
      y = Math.min(Math.max(y, 0), dimensions[1]);
      return { x, y }
    });
    controlPointsInVolume.sort((p1, p2) => p1.x - p2.x);

    return controlPointsInVolume;
  }

  // -------------------- PANORAMIC PANEL -------------------- //

  public handleProtocolEnter = (displaySetUID: string) => {
    const { viewportGridService } = this.servicesManager.services;

    this.setDisplayed(true);

    const eventDentascanIndex = this.EVENTS.DENTASCAN_INDEX_UPDATED;
    const { unsubscribe } = this.subscribe(eventDentascanIndex, ({ displaySetUID }) => {
      this.renderPanoramic(displaySetUID);
    });
    this.subscriptions.push(unsubscribe);

    const eventChangedGrid = viewportGridService.EVENTS.ACTIVE_VIEWPORT_ID_CHANGED;
    const eventReady = viewportGridService.EVENTS.VIEWPORTS_READY;
    [eventChangedGrid, eventReady].forEach(evt => {
      // Small delay so active viewport ID effectively changed
      const { unsubscribe } = viewportGridService.subscribe(evt, ({ viewportId }) => {
        this.handleActiveViewportChange(viewportId);
      });
      this.subscriptions.push(unsubscribe);
    });

    const panoramicState = this.getPanoramicState(displaySetUID);
    if (!panoramicState) {
      // First initialization
      this.computePanoramic();
    } else {
      // Enable annotations on panoramic protocol
      const annotationUIDs = this.isEditing() ? panoramicState.annotationUIDs : panoramicState.annotationUIDs.slice(1);
      this.setAnnotationVisibility(displaySetUID, annotationUIDs, true);
    }

    this.addViewportListeners();
  }

  public handleProtocolExit = () => {
    this.setDisplayed(false);

    this.handleActiveViewportChange('default');

    for (const unsub of this.subscriptions) {
      unsub();
    }

    // Disable annotations on other protocols
    for (const displaySetUID in this.panoramicStates) {
      const { annotationUIDs } = this.getPanoramicState(displaySetUID);
      this.setAnnotationVisibility(displaySetUID, annotationUIDs, false);
    }
  }

  public handleActiveViewportChange = async (viewportId) => {
    const { viewportGridService, segmentationService } = this.servicesManager.services;

    await waitCondition(() => viewportGridService.getActiveViewportId() === viewportId);

    const activeDisplaySetsUIDs = viewportGridService.getDisplaySetsUIDsForViewport(viewportId);
    const activeSegmentation = segmentationService.getActiveSegmentation();
    const hiddenSegmentation = segmentationService.getHiddenSegmentation(activeSegmentation?.id);
    // Ensure the segmentation we are moving to is hidden
    if (hiddenSegmentation && activeDisplaySetsUIDs.includes(hiddenSegmentation.displaySetInstanceUID)) {
      for (const toolGroupId of segmentationService.getToolGroupIdsWithSegmentation(hiddenSegmentation.id)) {
        segmentationService.setActiveSegmentationForToolGroup(hiddenSegmentation.id, toolGroupId);
      };
    }
  }

  // Create dentascan segmentation when panoramic is created
  public handlePanoramicCreation = async (displaySetUID) => {
    const { segmentationService } = this.servicesManager.services;

    const activeSegmentation = segmentationService.getActiveSegmentation();
    if (!activeSegmentation) {
      return;
    }
    const dentascanSegmentations = segmentationService.getDentascanSegmentations();
    if (dentascanSegmentations.length) {
      return;
    }

    const panoramicState = this.getPanoramicState(displaySetUID);
    const dentascanDisplaySetUID = panoramicState.dentascanViewportData.data[0].displaySetInstanceUID;
    const toolGroupId = 'mpr';
    const LABELMAP = cstEnums.SegmentationRepresentations.Labelmap;
    for (const segmentation of segmentationService.getAxialSegmentations()) {
      const dentascanSegId = await segmentationService.createSegmentationForDisplaySet(
        dentascanDisplaySetUID, { label: segmentation.label }
      );

      await segmentationService.addSegmentationRepresentationToToolGroup(toolGroupId, dentascanSegId, true, LABELMAP);
      // Empty segment at index 0
      const segments = segmentation.segments.slice(1);
      for (const segment of segments) {
        const segmentConfig = {
          segmentIndex: segment.segmentIndex,
          toolGroupId: toolGroupId,
          properties: {
            label: segment.label,
            color: segment.color,
            opacity: segment.opacity,
            visibility: segment.isVisible,
            isLocked: segment.isLocked,
            active: segmentation.activeSegmentIndex === segment.segmentIndex
          }
        }
        segmentationService.addSegment(dentascanSegId, segmentConfig);
      }
    }
    // Reset active segmentation as it got modified
    segmentationService.setActiveSegmentationForToolGroup(activeSegmentation.id, toolGroupId);
  }

  protected getDentascanPointIndex = (evt: EventTypes.InteractionEventType) => {
    const { element: dentascanElement, currentPoints } = evt.detail;
    const { viewport: dentascanViewport } = getEnabledElement(dentascanElement);

    // Convert points from dentascan coordinates to axial coordinates
    const { imageData: dentascanImageData } = dentascanViewport.getImageData();
    const [dentascanK, layer, i] = dentascanImageData.worldToIndex(currentPoints.world) as Point3;

    return [dentascanK, layer, Math.round(i)];
  }

  protected getAxialPointIndex = (dentascan: Dentascan, dentascanK: number, layer: number) => {
    const [minPoint, _] = dentascan.interval;
    const [vx, vy] = dentascan.direction;
    // Dentascan pixels are obtained with the average value of two voxels from the dentascan segment
    // As the opposite operation cannot be done, we place the cursor at the corresponding coordinates
    // along the dentascan segment (float), instead of rounding to closer voxel
    const x = minPoint[0] + dentascanK * vx;
    const y = minPoint[1] + dentascanK * vy;
    const z = dentascan.height + dentascan.minLayer - layer;

    return [x, y, z];
  }

  protected getAxialOperationData = (
    evt: EventTypes.InteractionEventType,
    brushInstance: BrushTool,
    segmentIndex: number
  ) => {
    const { cornerstoneViewportService } = this.servicesManager.services;

    // Code modified from BrushTool.ts
    const axialViewport = cornerstoneViewportService.getCornerstoneViewport('default');
    const axialElement = axialViewport.element;

    const axialViewportInfo = cornerstoneViewportService.getViewportInfo('default');
    const axialVolumeData = axialViewportInfo.getViewportData().data[0] as VolumeData;
    const axialImageData = axialVolumeData.volume.imageData;

    const displaySetUID = axialVolumeData.displaySetInstanceUID;
    const panoramicState = this.getPanoramicState(displaySetUID);
    // dentascanK and layer are float, i is integer
    const [dentascanK, layer, i] = this.getDentascanPointIndex(evt);
    // Get dentascan as we need vector direction for _calculateCursor
    const dentascan = panoramicState.dentascans[i];

    const [x, y, z] = this.getAxialPointIndex(dentascan, dentascanK, layer) as Point3;
    const axialCenterCursorInWorld = axialImageData.indexToWorld([x, y, z]);

    // Reset _hoverData for calculating cursor
    brushInstance._hoverData = brushInstance.createHoverData(axialElement);
    brushInstance._calculateCursor(axialElement, axialCenterCursorInWorld);
    const { points } = brushInstance._hoverData.brushCursor.data.handles;

    const editData = brushInstance.createEditData(axialElement);
    const { preview, strategySpecificConfiguration, eraseFocusIndex } = brushInstance.configuration;
    // TODO viewPlaneNormal and viewUp can be retrieved because default viewport and dentascans are axial
    const { viewPlaneNormal, viewUp } = axialViewport.getCamera();
    const segRepresentation = cstSegmentation.activeSegmentation.getActiveSegmentationRepresentation('mpr');
    const { segmentationId, segmentationRepresentationUID } = segRepresentation;

    const operationData = {
      ...editData,
      points: points,
      segmentIndex,
      previewColors: preview.enabled ? preview.previewColors : null,
      viewPlaneNormal,
      toolGroupId: 'mpr',
      segmentationId,
      segmentationRepresentationUID,
      viewUp,
      strategySpecificConfiguration,
      eraseFocusIndex,
      // Provide the preview information so that data can be used directly
      preview: brushInstance._previewData?.preview,
    };

    return operationData;
  }

  private _downCallBack = (evt: EventTypes.InteractionEventType) => {
    const { viewportId, element, mouseButton } = evt.detail;
    // Add listener on MouseUp for segmentation on axial viewport
    // Add listener on MouseDrag and MouseUp for segmentation on each dentascan viewport
    if (mouseButton === 1) {
      // Left button down
      if (viewportId !== 'default') {
        element.addEventListener(cstEnums.Events.MOUSE_DRAG, this._dragCallBack as EventListener);
      }
      element.addEventListener(cstEnums.Events.MOUSE_UP, this._endCallBack as EventListener);
      element.addEventListener(cstEnums.Events.MOUSE_CLICK, this._endCallBack as EventListener);
    }
  }

  private _endCallBack = (evt: EventTypes.InteractionEventType) => {
    const { segmentationService, cornerstoneViewportService, displaySetService } = this.servicesManager.services;

    const { viewportId, element } = evt.detail;
    if (viewportId !== 'default') {
      element.removeEventListener(cstEnums.Events.MOUSE_DRAG, this._dragCallBack as EventListener);
    }
    element.removeEventListener(cstEnums.Events.MOUSE_UP, this._endCallBack as EventListener);
    element.removeEventListener(cstEnums.Events.MOUSE_CLICK, this._endCallBack as EventListener);

    const { displaySetInstanceUID, volume } =
      cornerstoneViewportService.getViewportInfo('default').getViewportData().data[0] as VolumeData;
    const [volumeWidth, volumeHeight, nbLayers] = volume.dimensions;

    const segmentations = segmentationService.getSegmentations().filter(
      segmentation => segmentation.displaySetInstanceUID === displaySetInstanceUID
    );

    const dentascanDisplaySet = displaySetService.getActiveDisplaySets().find(
      displaySet => displaySet.SeriesInstanceUID.includes('dentascan')
    );
    const dentascanSegmentations = segmentationService.getSegmentations().filter(
      segmentation => segmentation.displaySetInstanceUID === dentascanDisplaySet.displaySetInstanceUID
    );

    segmentations.forEach((segmentation, segmentationIdx) => {
      // Access to scalarData;
      const segmentationScalarData = segmentationService.getLabelmapVolume(segmentation.id).getScalarData();
      const dentascanSegId = dentascanSegmentations[segmentationIdx].id;
      const dentascanSegScalarData = segmentationService.getLabelmapVolume(dentascanSegId).getScalarData();

      const getPixel = (minX, minY, vxNormal, vyNormal, dentascanK) => {
        const xi = minX + dentascanK * vxNormal;
        const yi = minY + dentascanK * vyNormal;
        const xj = minX + (dentascanK + 1) * vxNormal;
        const yj = minY + (dentascanK + 1) * vyNormal;
        const [pxi, pxj, pyi, pyj] = [Math.floor(xi), Math.floor(xj), Math.floor(yi), Math.floor(yj)];
        if (xi < 0 || yi < 0 || xj >= volumeWidth || yj >= volumeHeight) {
          return {}
        };

        // Same pixel
        let px = pxi;
        let py = pyi;
        if (pxi !== pxj && pyi === pyj) {
          // Compare with pixel at x + 1
          const tx = (pxi < pxj) ? (1 - xi % 1) / vxNormal : -(xi % 1) / vxNormal; // Ratio of segment within pxi
          if (tx >= 0.5) {
            px = pxj;
          }
        } else if (pxi === pxj && pyi !== pyj) {
          // Compare with pixel at y + 1
          const ty = (pyi < pyj) ? (1 - yi % 1) / vyNormal : -(yi % 1) / vyNormal; // Ratio of segment within pyi
          if (ty >= 0.5) {
            py = pyj;
          }
        } else if (pxi !== pxj && pyi !== pyj) {
          // Compare with both pixels
          const tx = (pxi < pxj) ? (1 - xi % 1) / vxNormal : -(xi % 1) / vxNormal;
          const ty = (pyi < pyj) ? (1 - yi % 1) / vyNormal : -(yi % 1) / vyNormal;
          if (tx < ty) {
            if (ty <= 0.5) {
              // Take the edge voxel if it is in 50% of the segment or more
              px = pxj;
              py = pyj;
            } else if (tx < 0.5) {
              // Take the middle voxel otherwise
              px = pxj;
            }
          } else {
            if (tx <= 0.5) {
              px = pxj;
              py = pyj;
            } else if (ty < 0.5) {
              py = pyj;
            }
          }
        }
        return { px: px, py: py };
      }

      // Replace values of current scalar data
      const { dentascans } = this.getPanoramicState(displaySetInstanceUID);
      dentascans.forEach((dentascan, i) => {
        const [minPoint, maxPoint] = dentascan.interval;
        const [minX, minY] = minPoint.map(Math.floor);
        const [vxNormal, vyNormal] = dentascan.direction;

        const pixelCoordinates = [];
        for (let dentascanK = 0; dentascanK < dentascan.width; dentascanK++) {
          pixelCoordinates.push(getPixel(minX, minY, vxNormal, vyNormal, dentascanK));
        }

        for (let layer = 0; layer < nbLayers; layer++) {
          for (let dentascanK = 0; dentascanK < dentascan.width; dentascanK++) {
            const { px, py } = pixelCoordinates[dentascanK];
            if (px === undefined) {
              continue;
            }
            const volumeLayer = dentascan.height + dentascan.minLayer - layer;
            const volumeIndex = volumeLayer * volumeWidth * volumeHeight + py * volumeWidth + px;
            const dentascansIndex = i * dentascan.width * dentascan.height + layer * dentascan.width + dentascanK;

            dentascanSegScalarData[dentascansIndex] = segmentationScalarData[volumeIndex];
          }
        };
      });
      // Trigger rendering
      for (let i = 0; i < dentascanSegScalarData.length; i++) {
        dentascanSegScalarData[i] = dentascanSegScalarData[i];
      }
      cstSegmentation.triggerSegmentationEvents.triggerSegmentationDataModified(dentascanSegId);
    });
  }

  protected getPassiveSegmentationId = () => {
    const { segmentationService } = this.servicesManager.services;

    const activeSegmentation = cstSegmentation.activeSegmentation.getActiveSegmentation('mpr');
    const { segmentationId: activeSegId, representationData } = activeSegmentation;
    const activeSegVolumeId = (representationData.LABELMAP as LabelmapSegmentationDataVolume).referencedVolumeId;

    const activeSegmentations = segmentationService.getSegmentations().filter(
      segmentation => segmentation.representationData.LABELMAP.referencedVolumeId === activeSegVolumeId
    );
    const passiveSegmentations = segmentationService.getSegmentations().filter(
      segmentation => segmentation.representationData.LABELMAP.referencedVolumeId !== activeSegVolumeId
    );

    const activeSegIndex = activeSegmentations.findIndex(segmentation => segmentation.id === activeSegId);

    return passiveSegmentations[activeSegIndex].id;
  }

  protected activatePassiveSegmentation = (passiveSegId: string) => {
    const { segmentationService } = this.servicesManager.services;

    const segRepresentations = segmentationService.getSegmentationRepresentationsForToolGroup('mpr') as
      cstTypes.ToolGroupSpecificRepresentation[];
    const passiveSegRepresentationUID = segRepresentations.find(
      representation => representation.segmentationId === passiveSegId
    ).segmentationRepresentationUID;
    cstSegmentation.activeSegmentation.setActiveSegmentationRepresentation('mpr', passiveSegRepresentationUID);
  }

  private _dragCallBack = (evt: EventTypes.InteractionEventType) => {
    const { cornerstoneViewportService, toolGroupService } = this.servicesManager.services;

    const toolGroup = toolGroupService.getToolGroup('mpr') as cstTypes.IToolGroup;
    if (toolGroup.currentActivePrimaryToolName !== 'Brush') {
      return;
    }
    const brushInstance = toolGroup.getToolInstance('Brush');

    const { segmentationId: activeSegId } = cstSegmentation.activeSegmentation.getActiveSegmentation('mpr');
    const passiveSegId = this.getPassiveSegmentationId();
    this.activatePassiveSegmentation(passiveSegId);

    const segmentIndex = cstSegmentation.segmentIndex.getActiveSegmentIndex(passiveSegId);
    const axialOperationData = this.getAxialOperationData(evt, brushInstance, segmentIndex);

    const axialElement = cornerstoneViewportService.getCornerstoneViewport('default').element;
    const axialEnabledElement = getEnabledElement(axialElement);
    brushInstance.applyActiveStrategy(axialEnabledElement, axialOperationData);
    // Due to the current implementation, activatePassiveSegmentation switches back and forth
    // between axialSegmentation and dentascanSegmentation
    this.activatePassiveSegmentation(activeSegId);
  }

  public computePanoramic = async () => {
    const { cornerstoneViewportService, viewportGridService, uiNotificationService } = this.servicesManager.services;

    // Display progress bar in front-end
    this.processing = true;
    this._broadcastEvent(this.EVENTS.PANORAMIC_PROCESSING, { percent: 1 });
    uiNotificationService.show({
      title: 'Panoramic computation',
      message: 'The computation of the panoramic and dentascans may take a few seconds',
      type: 'info',
      duration: 3000,
    });

    // Ensure display is correct before computation
    if (!cornerstoneViewportService.getCornerstoneViewport('default')) {
      // Fix issue when protocol is on the focused volume3D
      this.commandsManager.runCommand('toggleOneUp');
      await waitCondition(() => cornerstoneViewportService.getCornerstoneViewport('default'));
    }

    const displaySetUID = viewportGridService.getDisplaySetsUIDsForViewport('default')[0];
    let panoramicState = this.getPanoramicState(displaySetUID);
    // No control points
    if (!panoramicState?.annotationUIDs.length) {
      await createControlPoints({ servicesManager: this.servicesManager });
    }

    this._broadcastEvent(this.EVENTS.PANORAMIC_PROCESSING, { percent: 50 });

    await createPanoramic({ extensionManager: this.extensionManager, servicesManager: this.servicesManager });
    panoramicState = this.getPanoramicState(displaySetUID);

    this._broadcastEvent(this.EVENTS.PANORAMIC_PROCESSING, { percent: 100 });
    await this.refreshPanoramicDisplay();

    //Triggers dentascanIndexUpdated event
    this.setDentascanIndex(displaySetUID, Math.round(panoramicState.panoramic.width / 2));

    this._broadcastEvent(this.EVENTS.PANORAMIC_CREATED, { displaySetUID: displaySetUID });
    this.processing = false;
  }

  public renderPanoramic = async (displaySetUID: string) => {
    const { hangingProtocolService, cornerstoneViewportService } = this.servicesManager.services;

    if (hangingProtocolService.getActiveProtocol().protocol.id !== 'panoramicViewport') {
      return;
    }

    // Ensure viewports are correctly displayed
    await waitCondition(() => cornerstoneViewportService.getRenderingEngine()._needsRender.size === 0);
    // TODO
    const nbExpectedViewports = hangingProtocolService.getActiveProtocol().viewportMatchDetails.size;
    await waitCondition(() => cornerstoneViewportService.getViewportIds().length === nbExpectedViewports);

    const viewportIds = cornerstoneViewportService.getViewportIds();
    this.deletePanoramicAnnotation();
    // TODO render only if panoramic
    this.generateNewPanoramicAnnotation(displaySetUID, viewportIds[1]);
    this.displayDentascans();
  }

  public deletePanoramicAnnotation = () => {
    const { measurementService } = this.servicesManager.services;

    //============================= DELETE ALL PREVIOUS DENTASCAN ANNOTATIONS =================//
    const dentascanMeasurements = measurementService.getMeasurements()
      .filter(m => {return m.toolName === 'ArrowAnnotate'});

    // Delete measurements that are not handmade (thus the interpolated line)
    for (const pm of dentascanMeasurements) {
      if (pm.metadata.dentascan) {
        this.commandsManager.runCommand('deleteMeasurement', { uid: pm.uid });
      }
    }
  }

  public generateNewPanoramicAnnotation = (displaySetUID: string, panoramicViewportId: string) => {
    const { cornerstoneViewportService, toolGroupService } = this.servicesManager.services;

    //================== DISPLAY DENTASCAN SECTION IN PANORAMIC AS ANNOTATION ===========================//
    const panoramicViewport = cornerstoneViewportService.getCornerstoneViewport(panoramicViewportId);
    if (!panoramicViewport) {
      // Panoramic Viewport not found
      return
    }

    // Rescale dentascanIndex with PixelSpacing
    // TODO displaySet may not be needed for spacing
    const panoramicState = this.getPanoramicState(displaySetUID);
    const pointX = this.dentascanIndex * panoramicState.dentascanViewportData.data[0].volume.spacing[0];
    const pointY =
      (panoramicState.panoramic.height - 1) * panoramicState.dentascanViewportData.data[0].volume.spacing[2];

    const pointUp = [pointX, 0, 0];
    const pointDown = [pointX, pointY, 0];

    // Get ArrowAnnotate instance in order to create new annotation indicating dentascan section on panoramic
    const toolGroup = toolGroupService.getToolGroupForViewport(panoramicViewport.id);

    const arrowAnnotateInstance = toolGroup.getToolInstance('ArrowAnnotate');
    arrowAnnotateInstance.addNewAnnotationWithMode(
      panoramicViewport.element, [pointUp, pointDown], 'dentascan', 'rgb(255, 255, 255)'
    );
  }

  public displayDentascans = () => {
    const { cornerstoneViewportService } = this.servicesManager.services;

    //================== DISPLAY WANTED DENTASCANS IN BOTTOM VIEWPORT ===========================//
    const viewportIds = cornerstoneViewportService.getViewportIds();
    const nbDentascanViewports = viewportIds.length - 2;
    if (nbDentascanViewports > 0) {
      const middleDentascanViewportIndex = Math.floor(nbDentascanViewports / 2);
      for (let i = 0; i < nbDentascanViewports; i++) {
        const { element } = cornerstoneViewportService.getCornerstoneViewport(viewportIds[i + 2]);
        // We also display the dentascans before and after the main one
        cstUtilities.jumpToSlice(element, {
          imageIndex: this.dentascanIndex + (i - middleDentascanViewportIndex) * 2,
          debounceLoading: true,
        });
      }
    }
  }

  public addViewportListeners = async () => {
    const { cornerstoneViewportService, hangingProtocolService } = this.servicesManager.services;

    // Ensure listeners are not added before dentascan viewports are accessible
    const nbExpectedViewports = hangingProtocolService.getActiveProtocol().viewportMatchDetails.size;
    await waitCondition(() => cornerstoneViewportService.getViewportIds().length === nbExpectedViewports);

    const { element } = cornerstoneViewportService.getCornerstoneViewport('default');
    element.addEventListener(cstEnums.Events.MOUSE_DOWN, this._downCallBack as EventListener);
    const viewportIds = cornerstoneViewportService.getViewportIds();
    for (let i = 2; i < viewportIds.length; i++) {
      const { element } = cornerstoneViewportService.getCornerstoneViewport(viewportIds[i]);
      element.addEventListener(cstEnums.Events.MOUSE_DOWN, this._downCallBack as EventListener);
    }
  }

  public refreshPanoramicDisplay = async () => {
    const { cornerstoneViewportService, viewportGridService } = this.servicesManager.services;

    const panoramicVpId = cornerstoneViewportService.getViewportIds()[1];
    const panoramicDisplaySetUID = viewportGridService.getDisplaySetsUIDsForViewport(panoramicVpId)?.[0];

    this.commandsManager.runCommand(
      'setHangingProtocol', { protocolId: 'panoramicViewport', stageIndex: 0, reset: true }
    );

    // Wait until displaySet is updated
    await waitCondition(() => {
      const newPanoramicVpId = cornerstoneViewportService.getViewportIds()[1];
      return panoramicDisplaySetUID !== viewportGridService.getDisplaySetsUIDsForViewport(newPanoramicVpId)?.[0];
    });
  }
}
