import {DicomMetadataStore, OHIF, ViewportGridService} from '@ohif/core';
import {ToolGroupManager} from '@cornerstonejs/tools';
import {applySmoothing, blurPanoramic, cubicSplineInterpolation, sharpen} from './computePanoramicUtils.js'
import FileLoaderService from '../../../../../platform/app/src/routes/Local/fileLoaderService.js'
import {dcmbufferToPng, pngToCanvas} from './pngUtils.js'
import {polynomialLSF} from './computeDentalCurveUtils.js'
import {PanoramicState} from '../../../../cornerstone/src/types/Panoramic'
import {CONSTANTS, metaData, VolumeViewport} from '@cornerstonejs/core'
import {getTangentVector, getDentascanSection, getNormalVector} from './computeDentascansUtils'
import vtkImageData from '@kitware/vtk.js/Common/DataModel/ImageData.js';

const skmeans = require('skmeans');
const metadataProvider = OHIF.classes.MetadataProvider;

interface POINTS {
    controlPoints: number[][];
    curvePoints: number[][];
    outerPoints: number[][];
    innerPoints: number[][];
}

interface CONSTANTS {
    NUMBER_BASE_POINTS: number;
    DENTASCAN_THICKNESS: number;
    DENTAL_CURVE_THICKNESS: number;
    DENTASCAN_THRESHOLD_VALUE: number;
    AIP_THRESHOLD_VALUE: number;
    TOLERANCE: number;
    DENTAL_CURVES_STEP: number;
    AXIAL_CURVES_STEP: number;
    DENTAL_CURVE_AIP_STEP: number;
    DENTAL_CURVE_AIP_THICKNESS: number;
}
let constants: CONSTANTS | null = null;

function initializeConstants(volumeSpacing) {
    constants = {
        NUMBER_BASE_POINTS: 11,
        DENTASCAN_THICKNESS: Math.round(28 / volumeSpacing),
        DENTAL_CURVE_THICKNESS: Math.round(16 / volumeSpacing),
        DENTASCAN_THRESHOLD_VALUE: 1100,
        AIP_THRESHOLD_VALUE: 70,
        TOLERANCE: 0.0001,
        DENTAL_CURVES_STEP: 1,
        AXIAL_CURVES_STEP: 1,
        DENTAL_CURVE_AIP_STEP: 2,
        DENTAL_CURVE_AIP_THICKNESS: Math.round(16 / volumeSpacing),
    }
}

function waitCondition(condition) {
    return new Promise((resolve, reject) => {
        const wait = () => {
            const result = condition();
            if (result) {
                return resolve(result);
            } else {
                setTimeout(() => wait(), 50);
            }
        }
        wait();
    });
}

const createDataset = (canvas, datasetOptions) => {
    return new Promise((resolve, reject) => {
        canvas.toBlob(async (blob) => {
            const fileLoaderService = new FileLoaderService(blob);
            const imageId = fileLoaderService.addFile(blob);
            const img = await fileLoaderService.loadFile(blob, imageId);
            const dataset = await fileLoaderService.getDataset(img, imageId, datasetOptions);
            await waitCondition(() => dataset.imageId);

            const { StudyInstanceUID, SeriesInstanceUID, SOPInstanceUID } = dataset;
            metadataProvider.addImageIdToUIDs(imageId, {
                StudyInstanceUID,
                SeriesInstanceUID,
                SOPInstanceUID,
                frameIndex: dataset.NumberOfFrames > 1 ? dataset.InstanceNumber : 1,
            });
            resolve(dataset);
        });
    });
}

const createViewportData = async (datasets, viewportType, { extensionManager, servicesManager }, initialImageIndex?) => {
    const { displaySetService, cornerstoneCacheService } = servicesManager.services;

    DicomMetadataStore.addInstances(datasets);
    const activeDisplaySets = displaySetService.getActiveDisplaySets();
    const displaySetAdded = [activeDisplaySets[activeDisplaySets.length - 1]];
    const viewportOptions = {
        toolGroupId: 'mpr',
        viewportType: viewportType,
        orientation: 'axial',
        initialImageOptions: {
            preset: 'middle',
        },
    };
    const dataSource = extensionManager.getDataSources()[0];
    const viewportData = await cornerstoneCacheService.createViewportData(
        displaySetAdded,
        viewportOptions,
        dataSource,
        initialImageIndex
    );

    return viewportData;
}

const loadMultipleBlobsInViewport = async (metadata, { extensionManager, servicesManager }) => {
    const options = {
        prefilledValues:{
            SeriesInstanceUID: metadata.SeriesInstanceUID,
            StudyInstanceUID: metadata.StudyInstanceUID,
            SOPInstanceUID: Math.random().toString(36).substring(2, 10),
            FrameOfReferenceUID: Math.random().toString(36).substring(2, 10)
        }
    };
    const nbBlobs = metadata.PixelData.length;
    const initialImageIndex = Math.round(nbBlobs / 2);

    const datasets = await Promise.all(
        metadata.PixelData.map(async (layerData, layerI) => {
            const layerSopInstanceUID = `${options.prefilledValues.SOPInstanceUID}.${layerI + 1}`;
            const datasetOptions = {
                prefilledValues: {
                    ...options.prefilledValues,
                    SOPInstanceUID: layerSopInstanceUID,
                    InstanceNumber: layerI + 1,
                    ImagePositionPatient: [ 0, 0, 0.4 * layerI]
                }
            };

            const pngBuf = await dcmbufferToPng({ ...metadata, PixelData: layerData }, true);
            const canvas = pngToCanvas(pngBuf);
            const dataset = await createDataset(canvas, datasetOptions);

            return dataset;
        })
    );
    const viewportData = await createViewportData(datasets, 'volume', { extensionManager, servicesManager }, initialImageIndex);
    return viewportData;
}

const loadBlobInViewport = async (metadata, { extensionManager, servicesManager }) => {
    const datasetOptions = {
        prefilledValues: {
            SeriesInstanceUID: metadata.SeriesInstanceUID,
            StudyInstanceUID: metadata.StudyInstanceUID
        }
    }
    const pngBuf = await dcmbufferToPng(metadata);
    const canvas = pngToCanvas(pngBuf);
    const dataset = await createDataset(canvas, datasetOptions);
    const datasets = new Array(metadata.Columns).fill(dataset);
    const initialImageIndex = Math.round(metadata.Columns / 2);
    const viewportData = await createViewportData(datasets, 'stack', { extensionManager, servicesManager }, initialImageIndex);
    return viewportData;
}

//____________________________ FOR PANORAMIC COMPUTATION _________________________//

const getMax = (arr) => {
    let len = arr.length;
    let max = -Infinity;

    while (len--) {
        max = arr[len] > max ? arr[len] : max;
    }
    return max;
}

// Width becomes height, and height becomes width
const transposeImage = (arr, width, height) => {
    const transposedArr = [];
    for (let i = 0; i < width; i++) {
        for (let j = 0; j < height; j++) {
            transposedArr.push(arr[j * width + i]);
        }
    }
    return transposedArr;
}

// Returns the scalar data projected on the sagittal axis, using the max of values along X axis
const getSagittalDataMax = (scalarData, p) => {
    const sagittalData = new Float32Array(p.layerHeight * p.nbLayers);
    // Iterate over Y and Z axes
    for (let k = 0; k < p.nbLayers; k++) {
        for (let j = 0; j < p.layerHeight; j++) {
            // Store max value along axis in sagittalData
            let maxVal = -Infinity;
            for (let i = 0; i < p.layerWidth; i++) {
                const value = scalarData[(k + p.minLayer) * p.layerSize + j * p.layerWidth + i];
                if (value > maxVal) {
                    maxVal = value;
                }
            }
            sagittalData[k * p.layerHeight + j] = maxVal;
        }
    }

    return sagittalData;
}

// Returns the scalar data projected on the coronal axis, using the max of values along X axis
const getCoronalDataMax = (scalarData, p) => {
    const coronalData = new Float32Array(p.layerWidth * p.nbLayers);
    // Iterate over Y and Z axes
    for (let k = 0; k < p.nbLayers; k++) {
        for (let j = 0; j < p.layerWidth; j++) {
            // Store max value along axis in coronalData
            let maxVal = -Infinity;
            for (let i = 0; i < p.layerHeight; i++) {
                const value = scalarData[(k + p.minLayer) * p.layerSize + j * p.layerHeight + i];
                if (value > maxVal) {
                    maxVal = value;
                }
            }
            coronalData[k * p.layerHeight + j] = maxVal;
        }
    }

    return coronalData;
}

// Returns the scalar data projected on the axial axis,
// using the mean values along Z axis between minLayer and maxLayer
const getAxialDataMean = (scalarData, minLayer, maxLayer, p) => {
    const nbToothLayers = maxLayer - minLayer;

    const axialData = new Float32Array(p.layerWidth * p.layerHeight);
    // Iterate over X and Y axes
    for (let i = 0; i < p.layerWidth; i++) {
        for (let j = 0; j < p.layerHeight; j++) {
            // Store average value along axis in axialData
            let sumVal = 0;
            for (let k = 0; k < nbToothLayers; k++) {
                sumVal += scalarData[(minLayer + k) * p.layerSize + j * p.layerWidth + i];
            }
            axialData[j * p.layerWidth + i] = sumVal / nbToothLayers;
        }
    }

    return axialData;
}

// I want to ensure that the polynomial does not diverge beyond the teeth zone.
// The goal is to smooth out and converge the polynomial on the center outside the given interval while
// preserving its original behavior within the interval [minX, maxX].
// To achieve this, I use a modified function incorporating two sigmoid functions.
const getDentalLiss = (f, minX, maxX) => {
    const dentascanWidth = constants.DENTASCAN_THICKNESS;
    // a1 and a2 are smoothing parameters.
    const a1 = 0.1;
    const a2 = 0.1;
    // c0 is the convergence on -inf, and c1 and +inf
    const c0 = dentascanWidth / 2;
    const c1 = dentascanWidth / 2;

    return (layer) => {
        const sigmoidLeft = 1 / (1 + Math.exp(-a1 * (layer - minX)));
        const sigmoidRight = 1 / (1 + Math.exp(a2 * (layer - maxX)));
        const originalDentalValue = f(layer);

        return originalDentalValue * sigmoidLeft * sigmoidRight
            + c0 * (1 - sigmoidLeft) + c1 * (1 - sigmoidRight);
    }
}

const getDentalCurve = (dentascan, p) => {
    const dentascanWidth = constants.DENTASCAN_THICKNESS;

    // Compute dental curve
    // Dentascan is thresholded to get only bone area
    const binarizedDentascan = dentascan.map(value => value > constants.DENTASCAN_THRESHOLD_VALUE ? value : -1000);
    // Transposed so that w => h and h => w. This allows us to LSF with a curve represented by an order 3 equation
    const transposedBin = transposeImage(binarizedDentascan, dentascanWidth, p.nbLayers);
    // Get the indexes of teeth zone and keep only indexes that are teeth (!= -1000)
    let transposedOnlyTeethIndex = Array.from(transposedBin).map((value, idx) => value === -1000 ? null
        : ({ x: idx % p.nbLayers, y: Math.floor(idx / p.nbLayers) })).filter(value => value);

    // Add points along the center of the dentascan to reduce the deformation of the curve
    for (let l = 0; l < p.nbLayers; l += 10) {
        transposedOnlyTeethIndex.push({ x: l, y: Math.floor(constants.DENTASCAN_THICKNESS / 2) });
    }

    // Curve fitting on a degree 3 polynomial
    let lsfDentalCurve = polynomialLSF(transposedOnlyTeethIndex, 3);
    // TODO order should depend on error for the curve fitting

    let lsfDentalLiss = (x) => constants.DENTASCAN_THICKNESS / 2;
    if (getMax(transposedBin) > -1000 && !lsfDentalCurve.equation.some((value) => isNaN(value))) {
        // minX and maxX where value is not -1000
        const minX = transposedBin.reduce((min, val, idx) =>
            ((idx % p.nbLayers < min && val != -1000) ? idx % p.nbLayers : min), Infinity);
        const maxX = transposedBin.reduce((max, val, idx) =>
            ((idx % p.nbLayers > max && val != -1000) ? idx % p.nbLayers : max), -Infinity);
        lsfDentalLiss = getDentalLiss(lsfDentalCurve.predict, minX, maxX);
    }

    return lsfDentalLiss;
}

const getDentalCurvesPointsXY = (dentascans, mode, p) => {
    const nbDentalCurves = Math.floor(p.panoramicWidth / constants.DENTAL_CURVES_STEP);
    const nbAxialCurves = Math.floor(p.nbLayers / constants.AXIAL_CURVES_STEP);

    let dentalCurvesPoints;
    if (mode === 'curveAIP') {
        // Precompute the values
        dentalCurvesPoints = new Float32Array(p.panoramicWidth * p.nbLayers);
        dentascans.forEach((dentascan, idx) => {
            const dentascanCurve = getDentalCurve(dentascan.dentascan, p);
            for (let layer = 0; layer < p.nbLayers; layer++) {
                dentalCurvesPoints[idx * p.nbLayers + layer] = dentascanCurve(layer);
            }
        });
    }

    const dentalCurvesPointsXY = new Array(nbDentalCurves * nbAxialCurves).fill(null);
    for (let i = 0; i < nbDentalCurves; i++) {
        const idx = constants.DENTAL_CURVES_STEP * i;
        const dentascan = dentascans[idx];
        const [dentascanMinPoint, dentascanMaxPoint] = dentascan.interval;
        const [dentascanVxNormal, dentascanVyNormal] = dentascan.direction;
        const idxInf = Math.max(idx - Math.floor(constants.DENTAL_CURVE_AIP_THICKNESS / 2), 0);
        const idxSup = Math.min(idx + Math.floor(constants.DENTAL_CURVE_AIP_THICKNESS / 2), p.panoramicWidth - 1);

        let dentalCurve;
        if (mode === 'default') {
            dentalCurve = getDentalCurve(dentascan.dentascan, p);

        } else if (mode === 'valueMIP') {
            let dentascansForMIP = [];
            for (let j = idxInf; j <= idxSup; j += constants.DENTAL_CURVE_AIP_STEP) {
                dentascansForMIP.push(dentascans[i].dentascan);
            }
            const dentascanMIP = new Float32Array(dentascan.length);
            for (let j = 0; j < dentascan.length; j++) {
                dentascanMIP[j] = getMax(dentascansForMIP.map(dentascanScalar => dentascanScalar[j]));
            }
            dentalCurve = getDentalCurve(dentascanMIP, p);
        }

        for (let l = 0; l < nbAxialCurves; l++) {
            const layer = constants.AXIAL_CURVES_STEP * l;

            let xPosInDentascan;
            if (mode === 'curveAIP') {
                let xPosInDentascans = [];
                for (let j = idxInf; j <= idxSup; j += constants.DENTAL_CURVE_AIP_STEP) {
                    xPosInDentascans.push(dentalCurvesPoints[j * p.nbLayers + layer]);
                }
                xPosInDentascan = xPosInDentascans.reduce((a, b) => a + b, 0) / xPosInDentascans.length;
            } else { // default or valueMIP
                xPosInDentascan = dentalCurve(layer);
            }
            if (xPosInDentascan < 0) {
                xPosInDentascan = 0;
            } else if (xPosInDentascan > constants.DENTASCAN_THICKNESS) {
                xPosInDentascan = constants.DENTASCAN_THICKNESS;
            }
            const x = dentascanMinPoint[0] + xPosInDentascan * dentascanVxNormal;
            const y = dentascanMinPoint[1] + xPosInDentascan * dentascanVyNormal;

            dentalCurvesPointsXY[l * nbDentalCurves + i] = {x: x, y: y, xpos: xPosInDentascan};
        }
    }

    return dentalCurvesPointsXY;
}

// Dilate bone zone to eventually fill disconnections in dental zone
const dilate = (image, width, height, iterations) => {
    const result = new Array(width * height).fill(-1000);

    const getPixel = (x, y) => image[y * width + x] || 0;

    for (let iteration = 0; iteration < iterations; iteration++) {
        for (let y = 1; y < height - 1; y++) {
            for (let x = 1; x < width - 1; x++) {
                const neighbors = [
                    getPixel(x - 1, y - 1), getPixel(x, y - 1), getPixel(x + 1, y - 1),
                    getPixel(x - 1, y), getPixel(x, y), getPixel(x + 1, y),
                    getPixel(x - 1, y + 1), getPixel(x, y + 1), getPixel(x + 1, y + 1)
                ];

                result[y * width + x] = Math.max(...neighbors);
            }
        }

        // Mettre  jour l'image pour la prochaine itration
        for (let i = 0; i < width * height; i++) {
            image[i] = result[i];
        }
    }

    return result;
}

// TODO O(2n) algorithm
// Get spatial clusters in binarized image (groups of pixels connected between them)
const spatialClustering = (image, width, height) => {
    const clusters = [];
    const visited = new Array(width * height).fill(false);

    const getNeighbors = (index) => {
        const neighbors = [];
        const x = index % width;
        const y = Math.floor(index / width);

        for (let i = Math.max(0, x - 1); i <= Math.min(width - 1, x + 1); i++) {
            for (let j = Math.max(0, y - 1); j <= Math.min(height - 1, y + 1); j++) {
                const neighborIndex = j * width + i;
                neighbors.push(neighborIndex);
            }
        }

        return neighbors;
    };

    const exploreCluster = (startIndex, cluster) => {
        const queue = [startIndex];

        while (queue.length > 0) {
            const currentIndex = queue.shift();

            if (!visited[currentIndex] && image[currentIndex] !== -1000) {
                visited[currentIndex] = true;
                cluster.push(currentIndex);

                const neighbors = getNeighbors(currentIndex);

                for (const neighborIndex of neighbors) {
                    if (!visited[neighborIndex] && image[neighborIndex] !== -1000) {
                        queue.push(neighborIndex);
                    }
                }
            }
        }
    };

    for (let i = 0; i < width * height; i++) {
        if (!visited[i] && image[i] !== -1000) {
            const newCluster = [];
            exploreCluster(i, newCluster);
            clusters.push(newCluster);
        }
    }

    return clusters;
}

const addControlPointAnnotations = (viewport: VolumeViewport, imageData: vtkImageData, points: POINTS, middleLayer: number) => {
    // Transpose control points from volume basis to viewport canvas basis
    const { controlPoints, curvePoints, outerPoints, innerPoints } = points;

    // Draw these control points in front on Viewport so that user can move them
    // Get ArrowAnnotate instance in order to create new interpolated annotation
    const annotationUIDs: string[] = [];
    const toolGroup = ToolGroupManager.getToolGroupForViewport(viewport.id, viewport.renderingEngineId);
    const arrowAnnotateInstance = toolGroup.getToolInstance('ArrowAnnotate');

    const addAnnotation = (pointsSet: number[][], mode: string) => {
        const pointsInWorld = pointsSet.map(cp => imageData.indexToWorld([cp[0], cp[1], middleLayer]));
        const pointAnnotation = arrowAnnotateInstance.addNewAnnotationWithMode(
            viewport.element, pointsInWorld, mode, 'rgb(0, 255, 0)'
        );
        annotationUIDs.push(pointAnnotation.annotationUID);
    }

    addAnnotation(controlPoints, 'movable');
    for (const pointsSet of [curvePoints, outerPoints, innerPoints]) {
        addAnnotation(pointsSet, 'points');
    }

    return annotationUIDs;
}

const deleteControlPointAnnotations = (viewport: VolumeViewport, annotationUIDs: string[]) => {
    const toolGroup = ToolGroupManager.getToolGroupForViewport(viewport.id, viewport.renderingEngineId);
    const arrowAnnotateInstance = toolGroup.getToolInstance('ArrowAnnotate');

    for (const annotationUID of annotationUIDs) {
        arrowAnnotateInstance.removeArrowAnnotation(annotationUID);
    }
}

const computeControlPoints = (f: Function, bounds: number[][]) => {
    // Compute control points : points that determine piecewise linear curve function through f
    // They are also the points drawn in Viewport so that user can modify if the curve is not correct
    // Curve control points are only used to display a pretty curve
    const curvePoints: number[][] = [];
    const innerPoints: number[][] = [];
    const outerPoints: number[][] = [];

    const [[minX, maxX], [minY, maxY]] = bounds;
    let x = maxX;
    do {
        let y = f(x);
        y = Math.min(Math.max(y, minY), maxY);
        curvePoints.push([x, y]);

        // Determine inner curve and outer curve
        const [vxNormal, vyNormal] = getNormalVector(f, x);
        let minPoint = [x - constants.DENTASCAN_THICKNESS / 2 * vxNormal, y - constants.DENTASCAN_THICKNESS / 2 * vyNormal];
        let maxPoint = [x + constants.DENTASCAN_THICKNESS / 2 * vxNormal, y + constants.DENTASCAN_THICKNESS / 2 * vyNormal];
        // Swap minPoint and maxPoint if minPoint is inner
        if (vyNormal < 0) {
            [minPoint, maxPoint] = [maxPoint, minPoint];
        }
        outerPoints.push(minPoint);
        innerPoints.push(maxPoint);

        x -= getTangentVector(f, x)[0];
    } while (x > minX);
    const controlPoints: number[][] = [];
    const step = (curvePoints.length - 1) / (constants.NUMBER_BASE_POINTS - 1);
    for (let i = 0; i < curvePoints.length + constants.TOLERANCE; i += step) {
        controlPoints.push(curvePoints[Math.floor(i)]);
    }

    const points: POINTS =
        { controlPoints: controlPoints, curvePoints: curvePoints, outerPoints: outerPoints, innerPoints: innerPoints };

    return points;
}

// Computation of panoramic control points, which are points in the dental curve
// from where panoramic will be computed
const createControlPoints = async ({ servicesManager }) => {
    const { cornerstoneViewportService, panoramicService } = servicesManager.services;

    const { displaySetInstanceUID, volume } =
        cornerstoneViewportService.getViewportInfo('default').getViewportData().data[0];
    const { scalarData, dimensions, imageData, spacing } = volume;
    const [layerWidth, layerHeight, _] = dimensions;

    initializeConstants(Math.min(...spacing));

    const minLayer = Math.floor(scalarData.findIndex((value) => value > -1000) / layerWidth / layerHeight);
    const maxLayer = Math.floor(scalarData.findLastIndex((value) => value > -1000) / layerWidth / layerHeight) + 1;
    // Ignore rows with empty data
     const parameters = {layerWidth: layerWidth, layerHeight: layerHeight, layerSize: layerWidth * layerHeight,
                        minLayer: minLayer, nbLayers: maxLayer - minLayer, };

    const coronalData = getCoronalDataMax(scalarData, parameters);

    // We select the highest value cluster on coronal view with a K-means++
    const skMeansCoronal = skmeans(coronalData, 6);
    const {centroids:centroidsCoronal, idxs:idxsCoronal} = skMeansCoronal
    const idxsMaxCoronal = centroidsCoronal.indexOf(Math.max(...centroidsCoronal))
    // Find minimal and maximal layer where one pixel is not -1000
    const minTissueLayer = minLayer + Math.floor(idxsCoronal.findIndex((value) => value === idxsMaxCoronal) / layerHeight);
    const maxTissueLayer = minLayer + Math.floor(idxsCoronal.findLastIndex((value) => value === idxsMaxCoronal) / layerHeight) + 1;
    console.log('MINLAYER, MAXLAYER', minTissueLayer, maxTissueLayer);
    console.log('CENTROIDS', centroidsCoronal);
    // TODO assert minLayer !== maxLayer

    const axialData = getAxialDataMean(scalarData, minTissueLayer, maxTissueLayer, parameters);

    // K-means++ in AIP axial image data to isolate bone / jaw zone
    const skMeansResult = skmeans(axialData, 5);
    const {centroids, idxs} = skMeansResult
    const maxClusterIdx = centroids.indexOf(getMax(centroids))

    // Binarize array between max cluster (highest values meaning bone pixels) and other
    let binarizedArray = Array.from(axialData.map((value, idx) => idxs[idx] === maxClusterIdx ? value : -1000));

    const binarizedArrayDilated = dilate(binarizedArray, layerWidth, layerHeight, 5);

    // Find equation of polynomial going through dental zone with the use of the biggest spatial cluster
    // corresponding to our dental zone
    /* const binarizedIdx = binarizedArray.map((value, idx) => value != -1000? idx: -1000);
    const filteredIdx = binarizedIdx.filter((value) => value != -1000);
    const binarizedXy = Array.from(filteredIdx).map(i => ({ x: i % layerWidth, y: Math.floor(i / layerWidth) }));*/

    const spatialClusters = spatialClustering(binarizedArrayDilated, layerWidth, layerHeight);
    spatialClusters.sort((sc1, sc2) => sc2.length - sc1.length);
    // Indexes of biggest spatial cluster as X and Y values
    const biggestSpatialClusterXy = spatialClusters[0].map(i => ({ x: i % layerWidth, y: Math.floor(i / layerWidth) }));

    // LSF method to get best fitting parabola along pixels in square
    let lsf = polynomialLSF(biggestSpatialClusterXy, 2);
    let f = lsf.predict;

    // Find minimum and maximum of our binarized to determine where panoramic should start and where should end
    // We want control points to start at right and left jaw, so we don't use biggestCluster in this case
    // since it corresponds to the dental zone, not jaws
    let minX = binarizedArrayDilated.reduce((min, val, idx) =>
        (idx % layerWidth < min && val != -1000 ? idx % layerWidth : min), Infinity);
    let maxX = binarizedArrayDilated.reduce((max, val, idx) =>
        (idx % layerWidth > max && val != -1000 ? idx % layerWidth : max), -Infinity);
    const bounds = [[minX, maxX], [0, layerHeight]];
    const middleLayer = Math.ceil((maxLayer - minLayer) / 2) - 1;
    const points = computeControlPoints(f, bounds);
    const viewport = cornerstoneViewportService.getCornerstoneViewport('default');
    const annotationUIDs = addControlPointAnnotations(viewport, imageData, points, middleLayer);

    const panoramicState = panoramicService.getPanoramicState(displaySetInstanceUID);
    const newPanoramicState: PanoramicState = {
        ...panoramicState,
        id: displaySetInstanceUID,
        annotationVisibility: false,
        annotationUIDs: annotationUIDs
    };
    panoramicService.addDisplaySetUID(displaySetInstanceUID);
    panoramicService.setPanoramicState(displaySetInstanceUID, newPanoramicState);
}

// Control points that can be changed in front will be re-used to compute
// A new dental curve thus a new panoramic
const createPanoramic = async ({ extensionManager, servicesManager }) => {
    const {
        cornerstoneViewportService,
        displaySetService,
        uiNotificationService,
        panoramicService,
        segmentationService,
    } = servicesManager.services;

    // Get control points
    const { displaySetInstanceUID, volume } =
        cornerstoneViewportService.getViewportInfo('default').getViewportData().data[0];
    const { scalarData, dimensions, imageData } = volume;

    const controlPointsInVolume = panoramicService._getControlPoints(displaySetInstanceUID, imageData, dimensions);
    if (!controlPointsInVolume.length) {
        return;
    }

    const [layerWidth, layerHeight, _] = dimensions;
    const minLayer = Math.floor(scalarData.findIndex((value) => value > -1000) / layerWidth / layerHeight);
    const maxLayer = Math.floor(scalarData.findLastIndex((value) => value > -1000) / layerWidth / layerHeight) + 1;
    const middleLayer = Math.ceil((maxLayer - minLayer) / 2) - 1;
    const nbLayers = maxLayer - minLayer;

    let parameters = {layerWidth: layerWidth, layerHeight: layerHeight, layerSize: layerWidth * layerHeight,
                      minLayer: minLayer, nbLayers: nbLayers};
    // LSF method to get best fitting parabola along basePoints
    const lsfDentalCurve = polynomialLSF(controlPointsInVolume, 6);
    const f = lsfDentalCurve.predict;

    // For now we just have control points
    // We need to regenerate base points, which are equitably distributed along the curve
    // The curve is bounded within the volume width
    const minX = Math.max(controlPointsInVolume[0].x, 0);
    const maxX = Math.min(controlPointsInVolume[constants.NUMBER_BASE_POINTS - 1].x, layerWidth);
    const bounds = [[minX, maxX], [0, layerHeight]];

    const viewport = cornerstoneViewportService.getCornerstoneViewport('default');
    const panoramicState = panoramicService.getPanoramicState(displaySetInstanceUID);
    deleteControlPointAnnotations(viewport, panoramicState.annotationUIDs);
    const points = computeControlPoints(f, bounds);
    const annotationUIDs = addControlPointAnnotations(viewport, imageData, points, middleLayer);

    // If user is asking to regenerate panoramic and there already is one for this series, delete the previous one
    if (panoramicState.panoramic?.width) {
        // Delete segmentations of previous dentascan
        for (const segmentation of segmentationService.getDentascanSegmentations()) {
            segmentationService.remove(segmentation.id);
        }
        displaySetService.deleteDisplaySet(panoramicState.panoramicViewportData.data[0].displaySetInstanceUID);
        displaySetService.deleteDisplaySet(panoramicState.dentascanViewportData.data[0].displaySetInstanceUID);
    }

    // Iteration on normals to determine width of panoramic and array of offsets
    // On each index representing a basePoints, the value represents the width value on the panoramic
    // from where the corresponding basePoint begins.
    // TODO

    let dentascans = [];

    // Compute panoramicWidth and build dentascans
    // Dentascans need to be stored from right to left to be in the correct order
    let x = maxX;
    do {
        const dentascan = getDentascanSection(x, f, scalarData, parameters, constants.DENTASCAN_THICKNESS);
        dentascans.push(dentascan);
        x -= getTangentVector(f, x)[0] / 2; // Anti-Aliasing
    } while (x > minX);
    let newDentascans = [];
    for (let i = 0; i < Math.floor(dentascans.length / 2); i++) {
        let dentascan = dentascans[2 * i];
        const newDentascanWidth = Math.floor(dentascan.width / 2);
        let newDentascan = new Float32Array(newDentascanWidth * nbLayers).fill(0);
        for (let j = 0; j < newDentascanWidth * nbLayers; j++) {
            newDentascan[j] += dentascans[2 * i].dentascan[2 * j];
            newDentascan[j] += dentascans[2 * i + 1].dentascan[2 * j];
            newDentascan[j] += dentascans[2 * i].dentascan[2 * j + 1];
            newDentascan[j] += dentascans[2 * i + 1].dentascan[2 * j + 1];
            newDentascan[j] = Math.floor(newDentascan[j] / 4);
        }
        dentascan.dentascan = newDentascan;
        dentascan.width = newDentascanWidth;
        newDentascans.push(dentascan);
    }
    dentascans = newDentascans;
    const panoramicWidth = dentascans.length;
    parameters['panoramicWidth'] = panoramicWidth;

    /*const [a, b, c] = lsfDentalCurve.equation;
    const dfdx = a * x + b;
    const fl = Math.sqrt(1 + dfdx * dfdx);
    const flPrim = (x) => 1/(2*a)*(fl*dfdx + Math.log(fl+dfdx));
    const panoramicWidth = flPrim(maxX) - flPrim(minX);*/ // TODO

    // This promise take a base point and an offset
    // It computes the dentascan corresponding to the offset and finishes
    // This needs to be separated from buildPanoramicPromise as we will do a transformation on
    // the dentascan curves consisting in recalculating the curves.
    // TODO

    // Compute the value of each dentascan at each layer in order to compute axial curves of each layer
    const dentalCurvesPointsXY = getDentalCurvesPointsXY(dentascans, 'curveAIP', parameters);
    const nbDentalCurves = Math.floor(panoramicWidth / constants.DENTAL_CURVES_STEP);
    const nbAxialCurves = Math.floor(nbLayers / constants.AXIAL_CURVES_STEP);

    //let axialCurvesPoints = new Array(nbAxialCurves * panoramicWidth).fill(null);
    // Convert an array of long curves points to an array of axial curves points.
    // The value inside newScalarData contains the x coordinate of the curve
    for (let l = 0; l < nbAxialCurves; l++) {
        break;
        const layer = 1 * l;
        const [inf, sup] = [Math.max(0, l), l + 1];
        const coordinatesSlice = dentascanCurvesPoints.slice(inf * nbDentalCurves, sup * nbDentalCurves).filter(value => value);
        const axialCurve = polynomialLSF(coordinatesSlice, 6);
        // TODO
        for (let i = 0; i < panoramicWidth; i++) {
            const dentascan = dentascans[panoramicWidth - i - 1];
            const middlePoint = dentascan.middlePoint;
            // We want to find the point where an axial curve crosses a dental segment
            // Having ax+bx+c the curve of the axial curve and dx + e the curve of the segment,
            // This is solved by solving the equation ax + (b-d)x + (c-e) = 0
            // Which admits two roots: -(b-d)-sqrt(delta)/2a and -(b-d)+sqrt(delta)/2a where delta is (b-d)-4a(c-e)
            /*let [a_, b_, c_] = axialCurve.equation; // TODO
            const [d_, e_] = dentascan.equation;
            let xyAxial = null;
            if (a_ != 0) {
                const [a, b, c] = [a_, b_ - d_, c_ - e_];
                const delta = b*b - 4*a*c;
                if (delta > 0) {
                    // Two roots
                    const root1 = (-b - Math.sqrt(delta)) / (2*a);
                    const root2 = (-b + Math.sqrt(delta)) / (2*a);
                    if (Math.abs(root1 - middlePoint[0]) < Math.abs(root2 - middlePoint[0])) {
                        xyAxial = [root1, axialCurve.predict(root1)];
                    } else {
                        xyAxial = [root2, axialCurve.predict(root2)];
                    }
                } else if (delta === 0) {
                    const root = -b / (2*a)
                    xyAxial = [root, axialCurve.predict(root)];
                }

            }*//* else {
                const root = (e_ - c_) - (b_ - d_);
                xyAxial = [root, axialCurve.predict(root)];
            }*/ // TODO
            const [minX, minY] = dentascan.interval[0];
            let xyAxial = null;
            let costAxial = layerHeight; // Big value;
            for (let i = 0; i < dentascan.width; i++) {
                const x = minX + i * dentascan.direction[0];
                const yAxial = axialCurve.predict(x);
                const yDental = minY + i * dentascan.direction[1];

                const cost = Math.abs(yAxial - yDental);
                if (cost < costAxial) {
                    costAxial = cost;
                    xyAxial = [x, yAxial];
                }
            }
            if (xyAxial) {
                let xPosInDentascan = Math.floor(constants.DENTASCAN_THICKNESS / 2
                                                    + (xyAxial[0] - middlePoint[0]) / dentascan.direction[0]);
                const yPosInDentascan = layer;

                if (xPosInDentascan < 0) {
                    xPosInDentascan = 0;
                }
                if (xPosInDentascan > constants.DENTASCAN_THICKNESS) {
                    xPosInDentascan = constants.DENTASCAN_THICKNESS;
                }
                // Image is transposed
                // TODO : handle sigmoid curve on new dental curve
                axialCurvesPoints[i * nbAxialCurves + l] = {x: yPosInDentascan, y: xPosInDentascan};
            }
        }
    } // TODO

    // This promise take a base point and an offset
    // It fills panoramicArray from basePoint to next basePoint
    // Offset as explained earlier is the starting width of the base point
    // TODO

    // Init panoramic array
    let panoramicNormalsIterative = new Float32Array(panoramicWidth * nbLayers).fill(-1000)

    const panoramicDepth = constants.DENTAL_CURVE_THICKNESS + 1;
    // From start of base point to start of other base point, incremented by vector direction between those two points
    for (let i = 0; i < panoramicWidth; i++) {
        let dentascan = dentascans[panoramicWidth - i - 1];
        const [dentascanMinPoint, dentascanMaxPoint] = dentascan.interval;
        const [dentascanVxNormal, dentascanVyNormal] = dentascan.direction;

        //const coordinatesSlice = axialCurvesPoints.slice(i * nbAxialCurves, (i + 1) * nbAxialCurves).filter(value => value);
        //const dentalCurve = polynomialLSF(coordinatesSlice, 3);
        for (let layer = 0; layer < nbLayers; layer++) {
            // TODO
            const { x, y, xpos } = dentalCurvesPointsXY[Math.floor(layer / constants.AXIAL_CURVES_STEP) * nbDentalCurves +
                                                        Math.floor(i / constants.DENTAL_CURVES_STEP)];

            // Get min and max point along normal of dentalCurve which length between the two is DENTAL_CURVE_THICKNESS
            let minPoint = [x - constants.DENTAL_CURVE_THICKNESS / 2 * dentascanVxNormal,
                            y - constants.DENTAL_CURVE_THICKNESS / 2 * dentascanVyNormal];
            let maxPoint = [x + constants.DENTAL_CURVE_THICKNESS / 2 * dentascanVxNormal,
                            y + constants.DENTAL_CURVE_THICKNESS / 2 * dentascanVyNormal];

            const valuesAlongNormal = [];

            // Get values of point along normal between min and max
            for (let k = 0; k < panoramicDepth; k++) {
                const xk = Math.floor(minPoint[0] + k * dentascanVxNormal);
                const yk = Math.floor(minPoint[1] + k * dentascanVyNormal);
                // TODO Values are now picked from the dentascans instead of being picked from the volume, so that anti-aliasing is applied.
                const xposk = k + Math.floor(xpos - panoramicDepth / 2);
                const scalarValue = dentascan.dentascan[layer * constants.DENTASCAN_THICKNESS + xposk];
                const scalarValueNormalized = Math.floor((scalarValue + 1000) / 16) / 255;
                //let scalarValue = Math.floor((scalarData[layer * layerSize + yk * layerWidth + xk] + 1000) / 16) / 255;
                //let scalarValue = (scalarData[layer * layerSize + yk * layerWidth + xk] + 1000) / 16;
                //if (scalarValue >= 50) {
                    //valuesAlongNormal.push(Math.exp(scalarValue / S));

                // Take data along normal vector where x and y are valid coordinates
                if (!isNaN(scalarValueNormalized)) {
                    // The following formula computes the factor depending on dental curve thickness, when scalarValue
                    // equals 1, as empiric tests showed factor 0.15 was the best for dental curve thickness of 40
                    const factor = 1 - Math.pow(0.85, 40 / constants.DENTAL_CURVE_THICKNESS);
                    valuesAlongNormal.push(1 - factor * Math.pow(scalarValueNormalized, 3));
                }
            }

            // Add mean of all values as value in panoramic result
            // TODO
            let absorption = valuesAlongNormal.reduce((a, b) => a * b, 1);
            //let mean = valuesAlongNormal.reduce((a, b) => a + b, 0) / valuesAlongNormal.length;
            //const max = getMax(valuesAlongNormal);
            //panoramicNormalsIterative[panoramicWidth * (nbLayers - layer - 1) + i] = S * Math.log(mean) * 16 - 1000;
            panoramicNormalsIterative[panoramicWidth * (nbLayers - layer - 1) + i] = (1 - absorption) * 255 * 16 - 1000;
        }
    }
    // Post processing
    let panoramicProcessed = new Array(panoramicWidth * nbLayers).fill(-1000);
    const gImage = blurPanoramic(panoramicNormalsIterative, panoramicWidth, nbLayers);

    for (let i = 0; i < panoramicWidth * nbLayers; i++) {
        const image0 = panoramicNormalsIterative[i];
        const gImage0 = gImage[i];
        const alpha = 0.9;
        panoramicProcessed[i] = alpha * image0 + (1 - alpha) * (image0 - gImage0);
    }
    /*const oldMin = Math.min(...panoramicNormalsIterative);
    const oldMax = Math.max(...panoramicNormalsIterative);
    const newMin = Math.min(...panoramicProcessed);
    const newMax = Math.max(...panoramicProcessed);
    const scalingFactor = (oldMax - oldMin) / (newMax - newMin);


    // Rescale values
    for (let i = 0; i < panoramicWidth * nbLayers; i++) {
        panoramicProcessed[i] = Math.round((panoramicProcessed[i] - newMin) * scalingFactor + oldMin);
    }

    // Array at start is filled with false
    // When a promise at a specific basePoint is done, its index goes to true
    // This array allows us to track state of the promises to display progress bar in front

    // Recursive function to execute promises in array
    // When promise at index x is done, update doneArray by putting true value and wait a bit
    // to call recursively function of x + 1 index.

    // Update progress value in front-end by getting each 250ms the number of true values in doneArray
    // THIS WORKS ONLY WHEN PARENT FUNCTION IS CALLED WITH debug = false
    const waitUntilArrayDone = (start) => {
        return new Promise<void>((resolve, reject) => {
            const waitForArray = (start) => {
                const completed = doneArray.filter(el => el).length
                const progress = (start + Math.round((completed * 100) / 2 / basePoints.length))
                setProcessingPourcent(progress)
                if (completed === basePoints.length) {
                    return resolve();
                } else {
                    setTimeout(() => waitForArray(start), 50)
                }
            }
            waitForArray(start)
        })
    }
    // Wait until panoramic is completed
    await waitUntilArrayDone(0)*/

    let sharpenedPanoramic = panoramicNormalsIterative;//sharpen(panoramicNormalsIterative, panoramicWidth, nbLayers)
    //sharpenedPanoramic = applySmoothing(sharpenedPanoramic, panoramicWidth, nbLayers)

    // Display panoramic in OHIF Viewport
    const imageId = panoramicService._getImageId(displaySetInstanceUID);
    const voiLutModule = metaData.get('voiLutModule', imageId);
    const imagePixelModule = metaData.get('imagePixelModule', imageId);
    const generalSeriesModule = metaData.get('generalSeriesModule', imageId);

    if (!cornerstoneViewportService.getCornerstoneViewport('default')) {
        uiNotificationService.show({
            title: `Panoramic Generation Error`,
            message: `No viewport detected.`,
            type: 'error',
            duration: 3000,
        });
        return;
    }
    const panoramicMetadata = {
        SeriesInstanceUID: generalSeriesModule.seriesInstanceUID + '.panoramic',
        StudyInstanceUID: generalSeriesModule.studyInstanceUID,
        BitsAllocated: imagePixelModule.bitsAllocated,
        BitsStored: imagePixelModule.bitsStored,
        Rows: nbLayers,
        Columns: panoramicWidth,
        WindowCenter: voiLutModule.windowCenter[0],
        WindowWidth: voiLutModule.windowWidth[0],
        PixelData: sharpenedPanoramic
    };
    const panoramicViewportData = await loadBlobInViewport(panoramicMetadata, { extensionManager, servicesManager });

    const dentascanObject = {
        SeriesInstanceUID: generalSeriesModule.seriesInstanceUID + '.dentascan',
        StudyInstanceUID: generalSeriesModule.studyInstanceUID,
        BitsAllocated: imagePixelModule.bitsAllocated,
        BitsStored: imagePixelModule.bitsStored,
        Rows: nbLayers,
        Columns: constants.DENTASCAN_THICKNESS,
        WindowCenter: voiLutModule.windowCenter[0],
        WindowWidth: voiLutModule.windowWidth[0],
        PixelData: dentascans.map(dentascan => dentascan.dentascan) // TODO
    };

    const dentascansViewportData = await loadMultipleBlobsInViewport(dentascanObject, { extensionManager, servicesManager });

    const newPanoramicState: PanoramicState = {
        id: displaySetInstanceUID,
        annotationUIDs: annotationUIDs,
        annotationVisibility: panoramicState.annotationVisibility,
        dentascanViewportData: dentascansViewportData,
        panoramicViewportData: panoramicViewportData,
        panoramic: {
            width: panoramicWidth,
            height: nbLayers
        },
        dentascans: dentascans,
    }
    panoramicService.setPanoramicState(displaySetInstanceUID, newPanoramicState);
}

